.main-container {
  flex-direction: column;
  gap: 32px;
  margin: 0 auto;
  padding: 180px 16px 60px;
  display: flex;
}

.contacts-info {
  flex-direction: column;
  gap: 12px;
  width: 100%;
  min-width: 288px;
  max-width: 330px;
  margin: 0 auto;
  display: flex;
}

.mail-tel-text {
  &:hover, &:active {
    color: var(--active-color);
  }
}

.mail-tel-link {
  color: var(--dark-color);

  &:hover, &:active {
    color: var(--active-color);
  }
}

.get-in-tought {
  flex-direction: column;
  gap: 12px;
  width: 100%;
  min-width: 288px;
  max-width: 330px;
  margin: 0 auto;
  display: flex;
}

.contact-form, .input-wrap {
  flex-direction: column;
  gap: 12px;
  display: flex;
}

.input-text {
  border: 1px solid var(--dark-color);
  background-color: #0000;
  align-items: center;
  height: 32px;
  padding-left: 12px;
  display: flex;

  &:hover, &:focus {
    border-color: var(--active-color);
  }
}

.textarea-text {
  border: 1px solid var(--dark-color);
  background-color: #0000;
  align-items: center;
  padding-left: 12px;
  display: flex;

  &:hover, &:focus {
    border-color: var(--active-color);
  }
}

.btn-get-in-tought {
  border: 1px solid var(--dark-color);
  background-color: var(--secondary-color);
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  height: 28px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.5;
  display: flex;

  &:hover, &:focus {
    background-color: var(--active-color);
  }
}

@media screen and (width >= 744px) {
  .main-container {
    flex-direction: row;
    width: 672px;
    padding: 160px 36px 60px;
  }

  .contacts-info {
    max-width: 420px;
  }

  .mail-tel-text, .mail-tel-link {
    font-size: 18px;
  }

  .contacts-text {
    height: 32px;
    font-size: 18px;
  }

  .get-in-tought {
    max-width: 420px;
  }

  .textarea-text {
    height: 60px;
  }
}

@media screen and (width >= 1200px) {
  .main-container {
    width: 1128px;
  }

  .contacts-info {
    max-width: 520px;
  }

  .mail-tel-text, .mail-tel-link {
    font-size: 22px;
  }

  .contacts-text {
    height: 40px;
    font-size: 22px;
  }

  .get-in-tought {
    max-width: 520px;
  }

  .input-text {
    height: 40px;
    font-size: 22px;
  }

  .textarea-text {
    height: 60px;
    font-size: 22px;
  }

  .btn-get-in-tought {
    border: 2px solid var(--dark-color);
    height: 40px;
    font-size: 24px;
  }
}
